div.bounce-enter,
div.bounce-enter:hover {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  transition: all 600ms;
  transition-timing-function: ease-in-out;
  max-height: 0px;
}

div.bounce-enter-active,
div.bounce-enter-active:hover {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  max-height: 500px;
}

div.bounce-leave,
div.bounce-leave:hover {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  transition: all 600ms;
  transition-timing-function: ease-in-out;
  max-height: 500px;
}

div.bounce-leave-active,
div.bounce-leave-active:hover {
  -webkit-transform: scale(0.1, 0.1);
          transform: scale(0.1, 0.1);
  max-height: 0px;
}

/* div prefixes so these will have greated precedence than the component styles themselves */

div.center-enter {
  padding: 0;
  background-color: #ffb100;
  transition: all 1000ms;
  transition-timing-function: ease-in-out;
  min-height: 0;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.5);
}

div.center-enter .winner-popup-opening-content {
  display: flex;
}

div.center-enter .winner-popup-opening-content span {
  font-size: 18px;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  transition: font-size 1000ms, opacity 1000ms;
  opacity: 1;
}

div.center-enter .winner-popup-open-content {
  max-height: 0;
  transition: opacity 1000ms 500ms;
  opacity: 0;
}

div.center-enter-active {
  max-height: 1000px;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  background-color: #ffffff;
  width: 90%;
  left: 50%;
  top: 50%;
  padding: 0.5em;
  box-sizing: border-box;
  box-shadow: 20px 20px 0px rgba(0, 0, 0, 0.5);
}


div.center-enter-active .winner-popup-opening-content span {
  font-size: 0px;
  opacity: 0;
}

div.center-enter-active .winner-popup-open-content {
  max-height: 100%;
  opacity: 1;
}

div.fade-enter {
  opacity: 0;
}

div.fade-enter-active {
  opacity: 1;
}

div.fade-leave {
  opacity: 1;
}

div.fade-leave-active {
  opacity: 0;
}
html,
body,
#root {
  height: 100vh;
  font-size: 14px;
  font-family: -apple-system;
  font-family: "proxima-nova", "-apple-system", "Helvetica Neue", sans-serif;
  color: #615f5f;
}
body {
  margin: 0;
}

@-webkit-keyframes fadeIn {
  from {
    background: rgba(0, 0, 0, 0);
  }
  to {
    background: rgba(0, 0, 0, 0.4);
  }
}

@keyframes fadeIn {
  from {
    background: rgba(0, 0, 0, 0);
  }
  to {
    background: rgba(0, 0, 0, 0.4);
  }
}

@-webkit-keyframes slideUp {
  from {
    bottom: -100%;
  }
  to {
    bottom: 0;
  }
}

@keyframes slideUp {
  from {
    bottom: -100%;
  }
  to {
    bottom: 0;
  }
}

html .ath-viewport {
  opacity: 1;
  background: rgba(0, 0, 0, 0.4);
  -webkit-animation: fadeIn 300ms;
          animation: fadeIn 300ms;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
}
html .ath-phone.ath-container,
html .ath-container {
  width: 100%;
  left: 0;
  bottom: 0;
  margin-left: 0;
  background: #fff;
  border-radius: 3px;
  padding: 3em;
  box-shadow: 3px 3px 0 rgba(0, 0, 0, 0.1);
  text-align: left;
  -webkit-animation: slideUp 300ms;
          animation: slideUp 300ms;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms;
}
html .ath-ios.ath-tablet:after,
html .ath-ios.ath-phone:after {
  width: 0;
  height: 0;
  bottom: 10px;
  top: auto;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 12px solid #cecece;
  margin-left: -5px;
  -webkit-transform: none;
          transform: none;
  background: none !important;
  box-shadow: none !important;
}

html .ath-container h3 {
  margin: 0;
  margin-bottom: 0.5em;
  margin-top: 15px;
  font-size: 20px;
}

html .ath-container p {
  text-shadow: none;
}

html .ath-container.ath-ios .ath-application-icon,
html .ath-application-icon {
  border-radius: 15px;
  margin-bottom: 1em;
  margin-left: 1em;
  margin-top: 1em;
  width: 70px;
  height: 70px;
  float: right;
}

html .ath-container.ath-android.ath-icon:before,
html .ath-container.ath-icon:before {
  width: 30px;
  left: 10px;
  top: 5px;
  background-color: transparent;
}

html .ath-ios.ath-phone:after {
  background: #2c9eff;
  box-shadow: 3px 0px 0 rgba(0, 0, 0, 0.1);
}

